<template>
  <div class="express">
    <div class="container">
      <img src="../../assets/express/banner-cabinet.png" alt="">
    </div>
    <div class="container text-center">
      <div class="flow"></div>
    </div>
    <div class="wrapper">
      <v-title :title="this.$t('m.normalQuestion')" 
               border-color="#ffc541" 
               fontColor="#383838"
               fontWeight="none"
               style="margin-bottom:32px"></v-title>
      <question :question="question"></question>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        question: [
          {
            question: '1、使用移动支付会出现银行卡被盗刷问题吗？',
            answer: '只要在安全的网络环境中使用移动支付，绝不会出现银行卡资金安全问题。我们的应用所使用的技术源自目前顶尖技术团队，经过多方验证，并已在中国、韩国、日本等地区具有成熟市场。'
          },
          {
            question: '2、如果手机不见了怎么办？  ',
            answer: '此时可以拨打官方客服电话冻结账户，或者使用其他手机登录账户修改密码等。'
          },
          {
            question: '3、没有网络可以用移动支付吗？',
            answer: '不可以的。这是一款基于网络环境的支付应用。'
          }
        ]
      }
    },
    created () {
      window.scrollTo(0, 0)
    }
  }
</script>
<style scoped lang="stylus">
  @import '../../common/stylus/mixin.styl'
  .express
    overflow hidden
    .container
      position relative
      height 500px
      img
        height 500px
        position absolute
        left -360px
    .text-center
      height 900px
      margin-top 50px
      margin-bottom 70px
      .flow
        display inline-block
        width 1120px
        height 900px
        bg-image('../../assets/express/pic-cabinet')
    .wrapper
      width 1200px
      margin 0 auto
      text-align left
      padding 0 0 82px 0
</style>


